.adobt-me {
  display: flex;
  height: 90vh;
  max-width: 1500px;
  margin: 0 auto;
  margin-top: 5vh;
  -webkit-perspective: 1000px;
  perspective: 1000px;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
}
.adobt-me > * {
  flex: 1 4 auto;
  padding: 2rem;
  border: 1rem double #1a1a1a;
  position: relative;
  background: #fff;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.1);
  overflow: scroll;
}
.adobt-me > *:first-child {
  flex-shrink: 1;
  flex-basis: 50%;
  -webkit-transform: translateX(50%) rotateY(6deg) translateX(-50%);
  transform: translateX(50%) rotateY(6deg) translateX(-50%);
}
.adobt-me > *:nth-child(2) {
  -webkit-transform: translateX(-50%) rotateY(-14deg) translateX(50%);
  transform: translateX(-50%) rotateY(-14deg) translateX(50%);
  border-left: 0;
  border-right: 0;
  min-width: 300px;
}
.adobt-me > *:last-child {
  flex-shrink: 1;
  flex-basis: 50%;
  -webkit-transform: translateX(-50%) rotateY(10deg) translateX(50%) scale(1.08)
    translateX(24px);
  transform: translateX(-50%) rotateY(10deg) translateX(50%) scale(1.08)
    translateX(24px);
}
